function pauseAnimation() {
  const pause = document.querySelector("#pause");

  pause.addEventListener("click", () => {
    const stamp = document.querySelector('#fw-stamp-mk');

    if(!stamp.classList.contains('stamp__pause')){
      stamp.classList.add('stamp__pause');
      pause.setAttribute("title", "Resume Animation");
    } else {
      stamp.classList.remove('stamp__pause');
      pause.setAttribute("title", "Pause Animation");
    }
  })
}
